import React, { Fragment } from 'react';
import { Listbox } from '@headlessui/react';
import { CheckIcon } from '../../icons';
import classNames from 'classnames';

export interface SelectOptionProps extends React.HTMLAttributes<HTMLLIElement> {
  readonly value: string;
  readonly as?: React.ElementType;
  readonly disabled?: boolean;
}

export const SelectOption: React.FC<SelectOptionProps> = ({ className, children, ...props }) => (
  <Listbox.Option {...props} as={Fragment}>
    {({ active, selected }) => (
      <li
        className={classNames(
          'p-3',
          {
            'bg-accent text-body1': active === true,
            'bg-inverseBackground text-inverseBody1': active === false,
          },
          className
        )}
      >
        {selected && <CheckIcon />}
        {children}
      </li>
    )}
  </Listbox.Option>
);
